.App {
  font-family: sans-serif;
  text-align: center;
}
.dayItem {
  height: 151px;
  border-right: 1px solid black;
}
.visitLine {
  display: flex;
  justify-content: center;
  border: 1px solid gray;
  width: 250px;
  font-size: 14px;
}
.emptyCell {
  width: 250px;
  height: 148px;
  border: 1px solid gray;
}
.emptyField {
  width: 75%;
  height: 42px;
}
.visitTime {
  width: 15%;
  text-align: center;
  height: 25px;
  padding-top: 17px;
  font-size: 10px;
  opacity: 0.1;
  border-left: 0.5px solid gray;
}
.doctorNames {
  height: 42px;
  display: grid;
  grid-template-columns: repeat(2, 15px);
}
.doctorNames div {
  border: 0.5px solid gray;
  padding-top: 2px;
}
.emptyField,
.visitTime,
.doctorNames {
  border-right: 0.5px solid gray;
  opacity: 0.3;
}
h2 {
  margin: 0 auto 5px auto;
}
.headerLine {
  width: 1400px;
  display: grid;
  grid-template-columns: repeat(5, 250px) 1fr 1fr;
}
.headerLine div {
  text-align: center;
  font-weight: bold;
  border: 1px solid gray;
}
#dateLine {
  /* width: 200px; */
  text-align: center;
}
.calendarContainer {
  width: 1400px;
  display: grid;
  grid-template-columns: repeat(5, 250px) 75px 75px;
}
.weekend {
  width: 75px;
  text-align: center;
  border: 1px solid black;
  border-top: 0;
}
.weekendHeader {
  border: 1px solid black;
  border-top: 0;
}
